import { useState } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  FileText,
  Download,
  Search,
  Filter,
  Eye,
  MessageSquare,
} from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { toast } from "sonner";
import { DocumentViewerInterface } from "@/components/DocumentViewerInterface";
import { PeerReviewDialog } from "@/components/shared/dialogs/PeerReviewDialog";
import { ResponseDialog } from "@/components/shared/dialogs/ResponseDialog";
import { CourseFile, PeerReview } from "./types";
import {
  downloadFromDataUrl,
  downloadTextFile,
  sanitizeFileName,
} from "@/lib/download";

interface AllFacultyFilesViewProps {
  files: CourseFile[];
  currentUser: string;
  onFilesChange?: (files: CourseFile[]) => void;
}

export function AllFacultyFilesView({
  files,
  currentUser,
  onFilesChange,
}: AllFacultyFilesViewProps) {
  const [searchTerm, setSearchTerm] = useState("");
  const [filterDepartment, setFilterDepartment] = useState("all");
  const [filterStatus, setFilterStatus] = useState("all");
  const [filterYear, setFilterYear] = useState("all");
  const [isViewOpen, setIsViewOpen] = useState(false);
  const [selectedFile, setSelectedFile] = useState<CourseFile | null>(null);
  const [isPeerReviewOpen, setIsPeerReviewOpen] = useState(false);
  const [isResponseOpen, setIsResponseOpen] = useState(false);
  const [selectedReview, setSelectedReview] = useState<PeerReview | null>(null);

  const handleView = (file: CourseFile) => {
    setSelectedFile(file);
    setIsViewOpen(true);
  };

  const handleDownload = (file: CourseFile) => {
    const safeName = sanitizeFileName(file.fileName, "course-file");
    if (file.documentUrl) {
      downloadFromDataUrl(file.documentUrl, safeName);
      toast.success(`Downloading ${file.fileName}`);
      return;
    }

    const baseName = safeName.replace(/\.[^/.]+$/, "");
    const summaryName = `${baseName || "course-file"}-summary.txt`;
    const summary = [
      `File Name: ${file.fileName}`,
      `Course: ${file.courseCode} - ${file.courseName}`,
      `Type: ${file.fileType}`,
      `Semester: ${file.semester}`,
      `Academic Year: ${file.academicYear}`,
      `Uploaded: ${file.uploadDate}`,
      `Faculty: ${file.facultyName}`,
      `Department: ${file.department}`,
      `Status: ${file.status ?? "Unknown"}`,
    ].join("\n");
    downloadTextFile(summary, summaryName);
    toast.success(`Downloaded summary for ${file.fileName}`);
  };

  const handlePeerReview = async (review: string) => {
    if (!selectedFile) return;

    const newReview: PeerReview = {
      id: `pr${Date.now()}`,
      reviewerName: currentUser,
      reviewDate: new Date().toISOString().split("T")[0],
      comment: review,
    };

    const updatedFile = {
      ...selectedFile,
      peerReviews: [...(selectedFile.peerReviews || []), newReview],
    };

    try {
      const response = await fetch(`/api/course-files/${selectedFile.id}`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ peerReviews: updatedFile.peerReviews }),
      });
      const data = await response.json();
      if (!response.ok) {
        toast.error(data.error || "Peer review failed");
        return;
      }
      onFilesChange?.(data.files);
      const refreshed = data.files
        .filter((file: CourseFile) => file.id === selectedFile.id)
        .reduce<CourseFile | undefined>((acc, file) => acc ?? file, undefined);
      setSelectedFile(refreshed ?? updatedFile);
      setIsPeerReviewOpen(false);
    } catch (error) {
      console.error("Peer review error:", error);
      toast.error("An error occurred while submitting review");
    }
  };

  const handleRespondToReview = async (response: string) => {
    if (!selectedFile || !selectedReview) return;

    const updatedFile = {
      ...selectedFile,
      peerReviews: selectedFile.peerReviews?.map((pr) =>
        pr.id === selectedReview.id
          ? {
              ...pr,
              facultyResponse: response,
              responseDate: new Date().toISOString().split("T")[0],
            }
          : pr,
      ),
    };

    try {
      const apiResponse = await fetch(`/api/course-files/${selectedFile.id}`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ peerReviews: updatedFile.peerReviews }),
      });
      const data = await apiResponse.json();
      if (!apiResponse.ok) {
        toast.error(data.error || "Response failed");
        return;
      }
      onFilesChange?.(data.files);
      const refreshed = data.files
        .filter((file: CourseFile) => file.id === selectedFile.id)
        .reduce<CourseFile | undefined>((acc, file) => acc ?? file, undefined);
      setSelectedFile(refreshed ?? updatedFile);
      setSelectedReview(null);
      setIsResponseOpen(false);
    } catch (error) {
      console.error("Response error:", error);
      toast.error("An error occurred while responding");
    }
  };

  const filteredFiles = files.filter((file) => {
    const matchesSearch =
      file.fileName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      file.courseCode.toLowerCase().includes(searchTerm.toLowerCase()) ||
      file.facultyName.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesDepartment =
      filterDepartment === "all" || file.department === filterDepartment;
    const matchesStatus =
      filterStatus === "all" || file.status === filterStatus;
    const matchesYear =
      filterYear === "all" || file.academicYear === filterYear;

    return matchesSearch && matchesDepartment && matchesStatus && matchesYear;
  });

  const uniqueDepartments = Array.from(new Set(files.map((f) => f.department)));
  const uniqueYears = Array.from(new Set(files.map((f) => f.academicYear)));

  if (isViewOpen && selectedFile) {
    return (
      <DocumentViewerInterface
        file={selectedFile}
        onBack={() => setIsViewOpen(false)}
        onDownload={handleDownload}
        onPeerReview={handlePeerReview}
        onRespondToReview={handleRespondToReview}
        currentUser={currentUser}
      />
    );
  }

  return (
    <Card>
      <CardContent className="pt-6 space-y-4">
        <div className="flex flex-col md:flex-row gap-4">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
            <Input
              placeholder="Search by file name, course, or faculty..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10"
            />
          </div>
          <Select value={filterDepartment} onValueChange={setFilterDepartment}>
            <SelectTrigger className="w-full md:w-48">
              <Filter className="h-4 w-4 mr-2" />
              <SelectValue placeholder="Department" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Departments</SelectItem>
              {uniqueDepartments.map((dept) => (
                <SelectItem key={dept} value={dept}>
                  {dept}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
          <Select value={filterStatus} onValueChange={setFilterStatus}>
            <SelectTrigger className="w-full md:w-48">
              <Filter className="h-4 w-4 mr-2" />
              <SelectValue placeholder="Status" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Status</SelectItem>
              <SelectItem value="Approved">Approved</SelectItem>
              <SelectItem value="Pending">Pending</SelectItem>
              <SelectItem value="Rejected">Rejected</SelectItem>
            </SelectContent>
          </Select>
          <Select value={filterYear} onValueChange={setFilterYear}>
            <SelectTrigger className="w-full md:w-48">
              <Filter className="h-4 w-4 mr-2" />
              <SelectValue placeholder="Academic Year" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Years</SelectItem>
              {uniqueYears.map((year) => (
                <SelectItem key={year} value={year}>
                  {year}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        <div className="border rounded-lg">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>File Name</TableHead>
                <TableHead>Faculty</TableHead>
                <TableHead>Course</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Peer Reviews</TableHead>
                <TableHead className="text-right">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredFiles.length === 0 ? (
                <TableRow>
                  <TableCell
                    colSpan={6}
                    className="text-center text-gray-500 py-8"
                  >
                    No files found matching your criteria.
                  </TableCell>
                </TableRow>
              ) : (
                filteredFiles.map((file) => (
                  <TableRow key={file.id}>
                    <TableCell className="flex items-center gap-2">
                      <FileText className="h-4 w-4 text-blue-600" />
                      <div>
                        <div>{file.fileName}</div>
                        <div className="text-sm text-gray-500">
                          {file.fileType}
                        </div>
                      </div>
                    </TableCell>
                    <TableCell>
                      <div>
                        <div>{file.facultyName}</div>
                        <div className="text-sm text-gray-500">
                          {file.department}
                        </div>
                      </div>
                    </TableCell>
                    <TableCell>
                      <div>{file.courseCode}</div>
                      <div className="text-sm text-gray-500">
                        {file.courseName}
                      </div>
                    </TableCell>
                    <TableCell>
                      {file.status && (
                        <Badge
                          className={
                            file.status === "Approved"
                              ? "bg-green-100 text-green-800"
                              : file.status === "Rejected"
                                ? "bg-red-100 text-red-800"
                                : "bg-yellow-100 text-yellow-800"
                          }
                        >
                          {file.status}
                        </Badge>
                      )}
                    </TableCell>
                    <TableCell>
                      <div className="flex items-center gap-1">
                        <MessageSquare className="h-4 w-4 text-gray-400" />
                        <span className="text-sm">
                          {file.peerReviews?.length || 0}
                        </span>
                      </div>
                    </TableCell>
                    <TableCell>
                      <div className="flex justify-end gap-2">
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => handleDownload(file)}
                        >
                          <Download className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => handleView(file)}
                        >
                          <Eye className="h-4 w-4 text-blue-600" />
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </div>

        <PeerReviewDialog
          open={isPeerReviewOpen}
          onOpenChange={setIsPeerReviewOpen}
          onSubmit={handlePeerReview}
          itemType="file"
          itemName={selectedFile?.fileName || ""}
        />

        <ResponseDialog
          open={isResponseOpen}
          onOpenChange={(open) => {
            setIsResponseOpen(open);
            if (!open) setSelectedReview(null);
          }}
          onSubmit={handleRespondToReview}
          itemType="file"
        />
      </CardContent>
    </Card>
  );
}
